
.Main {
  height: 100%;
}
.tabClass {
  width: 20px;
  -webkit-transition: width 1s;
  transition: width 1s;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  background: #FFCCCC;
}
@-webkit-keyframes leftMove {
from {
    width: 100px;
    opacity: 0;
}
to {
    width: 200px;
    opacity: 1;
}
}
@keyframes leftMove {
from {
    width: 100px;
    opacity: 0;
}
to {
    width: 200px;
    opacity: 1;
}
}
.tabClass:hover {
  width: 120px;
}
.tabClass:hover ul li {
  width: 120px;
}
.tabClass:hover ul li a {
  width: 120px;
}
.Main .tab {
  display: block;
  top: 40%;
  width: 0;
  padding: 0;
  position: absolute;
  list-style-type: none;
  padding-top: 30px;
  margin: 0;
  color: #333333;
}
.Main .tab li {
  -webkit-transition: width 1s;
  transition: width 1s;
  width: 0;
  margin: 10px 0;
  text-align: left;
  border-bottom: 1px solid #FF99CC;
}
.Main .tab li a {
  text-decoration: none;
  -webkit-transition: width 1s;
  transition: width 1s;
  display: inline-block;
  width: 0;
  overflow: hidden;
  padding-left: 20px;
}
.Main .tab .active {
  background: #FFCC99;
  padding: 8px 0 8px 0;
  font-size: 20px;
}
h1,
h2 {
  font-weight: normal;
}
a:link {
  color: #333333;
}
a:active {
  color: green;
}



